import uniSteps from "@/components/uni-steps/uni-steps.vue"

export default {
	data() {
		// "提交成功", "处理中", "处理完成"
		return {
			title: '',
			options: [{
				title: '提交成功'
			}, {
				title: '处理中'
			}, {
				title: '处理完成'
			}],
			active: 1,
			activeColor: 'green',
			imgList: [],
			currentOrder: {},
			id: '',
			datas: [],
			dataList:[],
			content:''
		}
	},
	components: {
		uniSteps
	},
	onLoad(options) {
		this.id = options.id;
		this.loadData();
	},
	onShow() {},
	onHide() {},
	methods: {
		loadData() {
			this._reqw.ipost({
				orderId: this.id
			}, "orderDetail").then(res => {
				res.result == 0 ? (this.datas.push(dataList[0]),this.dataList=res.dataList,this.imgList=res.imageList,this.content=res.content) : this.$api.tip(res.resultNote)
			}).catch(err => {})
		},
		seeDetails(n) {
			uni.previewImage({
				urls: this.imgList,
				count: this.imgList[n]
			})
		}
	},
}
